.review{
  padding: 150px 0 250px;
  position: relative;
  &__shape{
    &-small{
      @extend .process__shape-small;
      @include desktop-lg{
        width: 100%;
      }
    }
  }
  &__row{
    margin: 0 -15px;
    display: flex;
    @include desktop{
      margin: 0 -15px 200px;
    }
  }
  &__col{
    flex-basis: 50%;
    margin: 0 15px;
    position: relative;
    z-index: 1;
    .shape__circle, .shape-square{
      position: absolute;
      width: 150px;
      z-index: -1;
      top: -75px;
      left: -75px;
    }
    .shape-square{
      top: unset;
      bottom: 0;
    }
    &:first-child{
      transform: translateY(140px);
    }
  }
  &__item{
    text-align: center;
    background: $white;
    border-radius: 4px;
    box-shadow: 0px 70px 70px 0px rgba(25, 28, 33, 0.1);
    padding: 35px 30px;
    &:not(:last-child){
      margin-bottom: 30px;
    }
    .thumb{
      width: 110px; height: 110px;
      border-radius: 50%;
      margin-bottom: 50px;
      box-shadow: 0px 15px 39px 0px rgba(9, 21, 104, 0.1);
    }
    .ratings{
      margin-bottom: 30px;
      svg{
        path{
          fill: #f34498;
        }
      }
    }
    h4{
      font-weight: 600;
      margin-bottom: 15px;
    }
    p{
      font-size: 15px;
      line-height: 25px;
    }
    &.secondary{
      background: linear-gradient( 127deg, rgb(62,27,189) 0%, rgb(160,97,255) 100%);
      h4,p{
        color: $white;
      }
    }
    &.primary{
      background: linear-gradient( 127deg, rgb(244,61,155) 0%, rgb(255,142,127) 100%);
      h4,p{
        color: $white;
      }
      svg{
        path{
          fill: $white;
        }
      }
    }
  }
  &__content{
    p{
      font-size: 20px;
      margin-bottom: 50px;
    }
  }
  &__shape{
    &-bg{
      position: absolute;
      width: 100%;
      bottom: 0;
      z-index: -1;
    }
  }
}