.about{
  padding: 100px 0;
  position: relative;
  &__shape{
    &-small{
      position: absolute;
      width: 80%;
      height: 30%;
      top: 10%;
      left: 50%; transform: translateX(-50%);
      svg{
        width: 100%;
        height: auto;
      }
    }
    &-circle{
      position: absolute;
      width: 200px;
      right: 15%;
      bottom: -20%;
      z-index: 2;
    }
  }
  &__image{
    @include desktop{
      margin-top: 50px;
    }
    position: relative;
    img{
      width: 100%;
    }
    .topLike{
      position: absolute;
      width: 40px;
      left: 0; top: 0;
      // box-shadow: 0px 25px 53px 0px rgba(25, 28, 33, 0.25); 
    }
    .rightLike{
      position: absolute;
      width: 80px;
      // box-shadow: 0px 25px 53px 0px rgba(25, 28, 33, 0.25); 
      top: 30px; right: 0;
    }
    &-love{
      position: absolute;
      width: 75px;
      height: auto;
      top: -80px;
      right: 10%;
      svg{
        width: 100%;
        path{
          fill: #dc396a;
        }
      }
    }
  }
}