.hero{
  background: linear-gradient( 108deg, rgb(255,142,127) 0%, rgb(255,87,154) 100%);
  position: relative;
  overflow: hidden;
  padding: 200px 0;
  @include desktop{
    padding: 150px 0;
  }
  &__shape{
    &-bottomleft{
      position: absolute;
      width: 40%;
      bottom: 0;
      left: -10%;
      opacity: .52;
      img{
        width: 100%;
      }
    }
    &-topright{
      position: absolute;
      width: 40%;
      right: -5%;
      top: -15%;
      img{
        width: 100%;
      }
    }
    &-love-1{
      position: absolute;
      width: 75px;
      top: 10%;
      left: 5%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-love-2{
      position: absolute;
      width: 75px;
      bottom: 10%;
      left: 5%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-love-3{
      position: absolute;
      width: 75px;
      right: 7%;
      top: 10%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-love-4{
      position: absolute;
      width: 75px;
      bottom: 3%;
      right: 2%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-small{
      position: absolute;
      width: 80%;
      top: 5%;
      left: 10%;
      @include desktop-lg{
        width: 100%;
      }
      img{
        width: 100%;
      }
    }
  }
  &__content{
    @include desktop{
      text-align: center;
      margin-bottom: 80px;
    }
    h1,p,span{
      color: $white;
    }
    h1{
      font-size: 90px;
    }
    p{
      font-size: 17px;
      margin-bottom: 80px;
      @include desktop{
        margin-bottom: 40px;
      }
    }
    span{
      display: block;
      font-size: 15px;
      margin-left: 20px;
      @include desktop{
        margin-left: 0;
      }
    }
    .btn{
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: 1px;
      i{
        display: inline-block;
        font-size: 30px;
        transform: translateY(4px);
        margin-left: 25px;
        background-image: linear-gradient( -53deg, rgb(233,83,122) 0%, rgb(255,150,136) 100%);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent
      }
    }
  }
  &__image{
    position: relative;
    z-index: 1;
    @include desktop{
      text-align: center;
    }
    .middle{
      width: 100%;
      box-shadow: 0px 39px 53px 0px rgba(25, 28, 33, 0.07); 
      @include desktop{
        width: 75%;
      }
    }
    .leftCharacter{
      position: absolute;
      bottom: -15%; left: -30%;
      width: 250px;
      @include desktop{
        left: -5%;
      }
    }
    .rightCharacter{
      position: absolute;
      right: -25%; bottom: -22%;
      width: 300px;
      @include desktop{
        right: 0;
        bottom: -10%;
      }
    }
    .topLike{
      position: absolute;
      top: -50px; left: 20px;
      width: 40px;
      @include desktop{
        left: 20%;
      }
    }
    .rightLike{
      position: absolute;
      right: -28%; bottom: 21%;
      @include desktop{
        right: -5%;
      }
    }
    .leftTree{
      position: absolute;
      width: 280px;
      left: -30%;
      top: -20%;
      z-index: -1;
      transform: scaleX(-1);
      opacity: .3;
    }
    .rightTree{
      position: absolute;
      width: 250px;
      right: -20%;
      top: -15%;
      transform: scaleX(-1);
      z-index: -1;
      opacity: .3;
    }
  }
}